.header{
    display: flex;
    width: 100%;
    justify-content: space-between;
    position: absolute;
    height: 10vh;
    top: 0;
    align-items: center;
}
.bar_items{
    display: flex;
    align-items: center;
}
.main_logo{
    display: flex;
    align-items: center;
    margin: 0 0 0 20px  ;
}
.icon_title{
    font-weight:800;
}
.responsive_icon{
    position: absolute;
    right: 30px;
    top: 20px;
    display: none;
}


.responsive_menu{
    display: flex;
    flex-direction: column;
    background: #deefff;
    box-shadow: 0px 0px 5px gainsboro;
    top: 55px;
    position: absolute;
    right: 38px;
    z-index: 999999;
}
.master_responsive{
    display: none;
}