.search{
    width: 80vw;
    height: 25vh;
    margin: 0 auto;
    position: relative;
    top: -100px;
    background-color: white;
    border-radius: 20px;
    box-shadow: 2px 2px 20px rgb(235, 235, 235);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
}
.serach_para{
    font-size: 1.5em;
    margin: 0px 50px;
}
.search_input{
    display: flex;
    justify-content: space-around;
}
.search_input_set{
    position: relative;
    width: 25%;
    margin: 0px 10px;
}
.input_icon{
    position: absolute;
    right: 0;
    top: calc(60px - 42px);
    color: gray;
}