button{
    all: unset;
    cursor: pointer;
    text-align: center;
    padding: 10px;
}
.customBtn{
    color: blue;
}
.homeIcon{
    padding: 10px;
    text-align: center;
}
.btnbox{
    width: 80%;
    margin: 10px auto;
    box-shadow: 0px 0px 10px gainsboro;
    padding: 20px 30px;
    margin: 30px auto;
    border-radius: 20px;
    display: flex;
    justify-content: space-around;
}
.AdminNav{
    display: flex;
    justify-content: space-around;
    width: 100%;
}
.admin_btn{
    width: 100px;
    padding: 10px 0px;
    box-shadow: 0px 0px 5px gainsboro;
}
.container{
    width: 90%;
    margin: 0 auto;
    text-align: center;
}
.admin_input{
    width: fit-content;
}
table{
    width: 90%;
    margin: 0 auto;
    text-align: center;
    border-radius: 20px;
    overflow: hidden;
}

tr{
    background-color: antiquewhite;
}
td,th{
    padding: 10px;
}
