

@media screen and (max-width:1200px){
    .main{
        height: initial !important;
        overflow: initial !important;
    }
}

@media screen and (max-width:1000px){
    .home_box{
        width: 100% !important;
    }
    .responsive_icon{
        display: block;
    }
    .master_responsive{
        display: block;
    }
    .search{
        top: -10px !important;
    }
    .signup_foam{
        margin: 50px 0px 0px 0px;
        width: 100% !important;
    }
    .home_h1{
        font-size: 2rem;
    }
    .box{
        flex-direction: column;
        height: initial !important;
        align-items: center;
    }
    .bar_items{
        display: none;
    }
    .colreverse{
        flex-direction: column-reverse;
    }
}

@media screen and (max-width:720px){
    .home_box{
        margin: 20px 0px;
        padding:50px 10px;
    }
    .Home_info{
        flex-direction: column;
    }
    .search{
        height: auto !important;
        flex-direction: column !important;
    }
    .homeIcon{
        text-align: start;
    }
    .Home_info_box{
        text-align: start !important;
    }
    .search_input{
        flex-direction: column;
        align-items: center !important;
    }
    .search_input_set{
        width: 100% !important;
    }
    .AdminNav{
        flex-direction: column;
    }
}